@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  /* for debugging purpose
  outline: solid limegreen 1px;
  */
}

.module {
  font-family: "Roboto", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
}
.module .section__imagebox {
  display: flex;
  justify-content: center;
}
.module .section__imagebox img {
  width: 100%;
  max-width: 505px;
  object-fit: cover;
}
.module .description__button {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  background-color: #e4c1ae;
  color: white;
  width: 128px;
  height: 48px;
  gap: 10px;
  padding: 16px 40px;
  border: none;
  outline: none;
  border-radius: 32px;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
}
.module .section__description {
  margin-top: 1rem;
  max-width: 380px;
  font-style: normal;
  color: #121212;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.module .section__description .description__title {
  font-weight: 700;
  font-size: 42px;
  line-height: 50px;
}
.module .section__description .description__text {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
}